import React from "react";
import ReactGA from "react-ga";
import { database, randomImages } from "../helpers";
import { LinkPreview } from "@dhaiwat10/react-link-preview";
import Helmets from "./Helmets";
import Header from "./Header";
import Footer from "./Footer";

const BlogDetails = (props) => {
  const { history } = props;
  const [blogData, setBlogData] = React.useState({});

  React.useEffect(() => {
    const blogDetails = database.find((i) => i.title === decodeURIComponent(window.location.href.split("/blog/")[1]));
    if (!blogDetails) return history.push("/blogs");
    setBlogData(blogDetails);

    return () => {
      setBlogData({});
    };
  }, [history]);

  React.useEffect(() => {
    window.scrollTo(0, 0);
    ReactGA.pageview(window.location.hash);
  }, []);

  return (
    <>
      <Helmets title={blogData?.title} />

      <Header {...props} title={blogData?.title} />

      <div className="blog-info-section">
        <div className="blog-body">
          {blogData?.data?.map((data, index) => {
            const randomImage = randomImages();
            return (
              <div key={index} className="blog-details-card" style={{ flexDirection: index % 2 === 0 ? "row" : "row-reverse" }}>
                <div className="left-part">
                  {data?.video ? <iframe title={data?.title} src={data?.video} /> : <LinkPreview url={data?.link} showLoader={true} />}
                  <div className="left-part-default-bg">
                    <img alt="blog-detail-img" src={data?.image || randomImage} />
                  </div>
                </div>
                <div className="right-part">
                  {data?.title && <h2>{data?.title}</h2>}
                  {data?.text ? (
                    <h3>{data?.text}</h3>
                  ) : data?.texts ? (
                    data?.texts?.map((textData, textIndex) => <h3 key={textIndex}>{textData}</h3>)
                  ) : (
                    ""
                  )}
                  {data?.link ? (
                    <h3>
                      <a href={data?.link} target="_blank" rel="noreferrer">
                        {data?.link}
                      </a>
                    </h3>
                  ) : data?.links ? (
                    data?.links?.map((linkData, linkIndex) => (
                      <h3 key={linkIndex}>
                        <a href={linkData} target="_blank" rel="noreferrer">
                          {linkData}
                        </a>
                      </h3>
                    ))
                  ) : null}
                </div>
              </div>
            );
          })}
        </div>
      </div>

      <Footer />
    </>
  );
};

export default React.memo(BlogDetails);
