import React from "react";
import { database, getRandomColor } from "../helpers";
import ReactGA from "react-ga";
import Helmets from "./Helmets";
import Header from "./Header";
import Footer from "./Footer";
import { Link } from "react-router-dom";

const Blogs = (props) => {
  const [searchText, setSearchText] = React.useState("");
  // const { history } = props;

  React.useEffect(() => {
    ReactGA.pageview(window.location.hash);
  }, []);

  const GA = (blogName) => {
    ReactGA.event({
      category: "Blog",
      action: `Clicked on ${blogName}`,
    });
  };

  return (
    <>
      <Helmets title="Urvesh Dungrani | Blogs" />

      <Header {...props} />

      <div className="blog-info-section">
        <div className="blog-body">
          <input
            autoFocus
            className="blog-search-input"
            placeholder="Search blogs here..."
            value={searchText}
            onChange={({ target }) => setSearchText(target.value)}
          />
          {database.map((data, index) => {
            if (data?.category?.toLowerCase().includes(searchText.toLowerCase()) || data?.title?.toLowerCase().includes(searchText.toLowerCase())) {
              const background = getRandomColor();
              return (
                <Link
                  to={`blog/${encodeURIComponent(data?.title)}`}
                  onClick={() => GA(data?.title)}
                  key={index}
                  className="blog-card"
                  // onClick={() => history.push(`blog/${encodeURIComponent(data?.title)}`)}
                >
                  <div className="blog-card-left-color-bar" style={{ background }} />
                  <h2 className="blog-card-category">{data?.category}</h2>
                  <h3>
                    {data?.title}
                    {data?.time && <div className="blog-card-date">Posted at {data?.time}</div>}
                  </h3>
                  <div className="blog-card-right-color-bar" style={{ background }} />
                </Link>
              );
            }
          })}
        </div>
      </div>

      <Footer />
    </>
  );
};

export default React.memo(Blogs);
