import React from "react";
import ReactGA from "react-ga";
import { getRandomGradient } from "../helpers";

export const ReachInfo = "reach_info";

const Header = ({ title, shouldShowBlogsLink = false, history }) => {
  const GA = () => {
    history.push("/blogs");
    ReactGA.event({
      category: "Blogs",
      action: "Clicked on Blogs button from Home page",
    });
  };

  return (
    <>
      <div className="front-section color-footer">
        <header>
          <div className="header-content-left">
            {/* <div className="logo-container"></div> */}
            {/* <div className="logo-drop"></div> */}
            <div className="name" onClick={() => history.push("/")}>
              Urvesh Dungrani
              <div className="occ">Full Stack Developer</div>
            </div>
            {/* <div className="logo-drop"></div>
            <div className="occ heart" onClick={() => history.push("/blogs")}>
              Blogs
            </div> */}
          </div>
          <div className="header-content-right">
            <div
              className="hello"
              onClick={() => {
                history.push("/");
                setTimeout(() => document.getElementById(ReachInfo).scrollIntoView(), 300);
              }}
            >
              <span className="hand">👋</span> Say hello!
            </div>
          </div>
        </header>

        <div className="hero-content">
          {title ? (
            <h2>
              <span className="white">{title}</span>
            </h2>
          ) : (
            <h1>
              <span className="white">Think. </span>
              <span className="blue">Learn. </span>
              <span className="green">Make. </span>
              <span className="red">Solve.</span>
              <span className="repeat-text"></span>
            </h1>
          )}
        </div>
      </div>

      {shouldShowBlogsLink && (
        <div className="blog-button heart" style={{ backgroundImage: getRandomGradient() }} onClick={GA}>
          Blogs
        </div>
      )}
    </>
  );
};

export default React.memo(Header);
